.wp-block-blocks-gamestore-bestseller-products {
  margin-top: 0;
  margin-bottom: clamp(3.75rem, calc(2.77rem + 4.92vw), 7.5rem)!important;

  h2 {
    line-height: 1.4;
    margin:0;
    width: fit-content;
    position: relative;
    z-index: 1;
    display: block;

    span {
      background: var(--accent-gradient);
      background-clip: text;
      -webkit-background-clip: text;
      -webkit-text-fill-color: transparent;
    }
  }

  .similar-top {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 24px;

    .right-similar-top {
      display: flex;
      gap: 8px;
      align-items: center;

      .view-all-link {
        border-radius: 5px;
        background: var(--white-contrast);
        padding: 20px 32px;
        color: var(--black-contrast, #FFF);
        font-size: 14px;
        font-weight: 700;
        line-height: 1;
        letter-spacing: 2.1px;
        text-transform: uppercase;
        text-decoration: none;
      }
      .similar-navigation {
        display: flex;
        gap: 8px;
        align-items: center;

        > div {
          background: var(--accent-gradient);
          border-radius: 100%;
          display: block;
          width: 64px;
          height: 64px;
          z-index: 9;
          display: flex;
          align-items: center;
          justify-content: center;
          cursor: pointer;

          &::before {
            content: "";
            width: 24px;
            height: 24px;
            display: block;
            background: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none"><path d="M14.9998 19.9201L8.47984 13.4001C7.70984 12.6301 7.70984 11.3701 8.47984 10.6001L14.9998 4.08008" stroke="white" stroke-width="1.5" stroke-miterlimit="10" stroke-linecap="round" stroke-linejoin="round"/></svg>');
          }
        }
        .similar-right {
          &::before {
            background: url('data:image/svg+xml,<svg xmlns="http://www.w3.org/2000/svg" width="24" height="24" viewBox="0 0 24 24" fill="none"><path d="M7.9025 19.9201L14.4225 13.4001C15.1925 12.6301 15.1925 11.3701 14.4225 10.6001L7.9025 4.08008" stroke="white" stroke-width="1.5" stroke-miterlimit="10" stroke-linecap="round" stroke-linejoin="round"/></svg>');
          }
        }
      }
    }
  }

  .bestseller-games-list {
    overflow: hidden;
    margin-left: -8px;
    margin-right: -8px;

    .swiper-wrapper {
      display: flex;
    }

    .swiper-slide {
      padding: 0 8px;
    }
  }
  .games-list {
    display: flex;
    gap: 16px;
    align-items: flex-start;
    flex-wrap: wrap;

    .game-result {
      max-width: 243px;

      .game-meta h3 {
        color: var(--text-primary, #0E0D0F);
      }
    }
  }
}